fieldset {
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  font-weight: 600;
  margin-bottom: 0.25rem;
  padding: 0;
}

label {
  display: inline-block;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

input,
select {
  display: block;
  font-family: "Lato";
  font-size: 1rem;
}

input,
textarea,
select {
  background-color: white;
  border: 1px solid #eee;
  box-shadow: inset 0 1px 0.1875rem rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  font-family: "Lato";
  font-size: 1rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
  transition: border-color 300ms ease-in-out;
  width: 100%;
}
input:hover,
textarea:hover,
select:hover {
  border-color: shade(#eee, 20%);
}
input:focus,
textarea:focus,
select:focus {
  border-color: #205883;
  box-shadow: inset 0 1px 0.1875rem rgba(0, 0, 0, 0.06), 0 0 0.3125rem rgba(27, 74, 111, 0.7);
  outline: none;
}
input:disabled,
textarea:disabled,
select:disabled {
  background-color: shade(white, 5%);
  cursor: not-allowed;
}
input:disabled:hover,
textarea:disabled:hover,
select:disabled:hover {
  border: 1px solid #eee;
}

textarea {
  resize: vertical;
}

[type=search] {
  appearance: none;
}

[type=checkbox],
[type=radio] {
  display: inline;
  margin-right: 0.25rem;
}

[type=file] {
  margin-bottom: 0.5rem;
  width: 100%;
}

select {
  margin-bottom: 1.5rem;
  max-width: 100%;
  width: auto;
}

._form select {
  width: 100%;
}
._form button {
  margin-top: 1rem;
}

.site-inner .gform_wrapper {
  margin: 0;
}
.site-inner .gform_wrapper ul {
  margin-left: 0 !important;
}
.site-inner .gform_wrapper ul li {
  list-style-type: none !important;
  padding: 0 !important;
}
.site-inner .gform_wrapper ul li:first-child {
  margin-top: 0;
}
.site-inner .gform_wrapper ul li input,
.site-inner .gform_wrapper ul li select {
  width: 100% !important;
}
.site-inner .gform_wrapper .gform_footer {
  margin: 0;
}

#tinymce ul,
.site-inner ul {
  list-style-type: none;
  margin: 0;
  margin-bottom: 1.75rem;
  padding: 0;
}
#tinymce ul li,
.site-inner ul li {
  list-style-type: none;
  margin-bottom: 0.625rem;
  padding-left: 1.5rem;
  position: relative;
}
#tinymce ul li:before,
.site-inner ul li:before {
  background-color: #555;
  border-radius: 50%;
  content: "";
  height: 0.5rem;
  left: 0.25rem;
  position: absolute;
  top: 0.5rem;
  width: 0.5rem;
}
#tinymce ul li ul,
.site-inner ul li ul {
  margin-bottom: 0;
  margin-top: 0.5rem;
}

#tinymce ol,
.site-inner ol {
  margin: 0;
  margin-bottom: 1.5rem;
  margin-left: 0;
  padding: 0;
}
#tinymce ol li,
.site-inner ol li {
  margin-bottom: 0.75rem;
  margin-left: 1.5rem;
  position: relative;
}
#tinymce ol li ol,
.site-inner ol li ol {
  margin-bottom: 0;
  margin-top: 0.5rem;
}

dl {
  margin-bottom: 1.5rem;
}
dl dt {
  margin-top: 0.5rem;
}
dl dd {
  margin: 0;
}

dl,
ol,
ul,
p {
  margin-bottom: 1.5rem;
}

table {
  border-collapse: collapse;
  margin: 0.5rem 0;
  table-layout: fixed;
  width: 100%;
}

th {
  border-bottom: 1px solid shade(#eee, 25%);
  font-weight: 600;
  padding: 0.5rem 0;
  text-align: left;
}

td {
  border-bottom: 1px solid #eee;
  padding: 0.5rem 0;
}

tr,
td,
th {
  vertical-align: middle;
}

html {
  font-size: 90%;
  scroll-behavior: smooth;
}
@media screen and (min-width: 56.25rem) {
  html {
    font-size: 1rem;
  }
}

html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

:target {
  scroll-margin-top: 2rem;
}

body {
  color: #555;
  font-family: "Lato";
  line-height: 1.5;
  background-color: white;
}
body *::selection {
  background-color: #205883;
